#top{
    .row--white-offset{
        background-color: $white;
        border-radius: 20px;
        box-shadow: 0px 15px 60px #304d7c;
        max-width: 970px;
        margin: 0 auto;
        @media only screen and (max-width: 835px){
            display: flex;
            flex-direction: column;
        }
        .flex_column{
            @media only screen and (max-width: 835px){
                width: 100%;
            }
            .flex-column-inner{
                display: flex;
                align-items: center;
                color: $darkblue2;
                @media only screen and (min-width: 836px){
                    min-height: 239px;
                }
            }
            &:first-child{
                .flex-column-inner{
                    justify-content: center;
                    padding: 20px;
                    .av-special-heading{
                        max-width: max-content;
                        .av-special-heading-tag {
                            max-width: max-content;
                        }
                    }
                }
            }
            &:last-child{
                .flex-column-inner{
                    padding: 30px 70px 30px 0;
                    @media only screen and (max-width: 835px){
                        padding: 20px;
                    }
                    p{
                        margin-bottom: 0;
                        font-size: 16px;
                    }
                }
            }
        }
    }
}