#top{
    .single-search-hero{
        background-size: cover!important;
        background-attachment: fixed!important;
        .container{
            .content{
                .search-title{
                    color: $pink4;
                    margin-bottom: 30px;
                }
                .search-result,
                .search-form-label{
                    color: $blue2;
                    letter-spacing: 0;
                }
                .search-form-inner{
                    margin: 20px 0;
                    form{
                        display: flex;
                        flex-wrap: wrap;
                        @media only screen and (max-width: 767px){
                            flex-direction: column;
                        }
                        input[type=text]{
                            max-width: 500px;
                            width: 100%;
                            padding: 15.5px 20px;
                            border-radius: 40px;
                            border: none;
                            box-shadow: 0 0 4px rgba($color: $black, $alpha: 0.5);
                            margin-right: 20px;
                            font-family: $body-font-family;
                            font-size: 15px;
                            outline: none;
                            @media only screen and (max-width: 767px){
                                margin-bottom: 20px;
                                max-width: 100%;
                                margin-right: 0;
                            }
                        }
                        .submit-search{
                            border: none;
                            cursor: pointer;
                        }
                    }
                }
            }
        }
    }
    .single-search-content{
        .entry-search{
            &:not(:last-child){
                margin-bottom: 50px;
                position: relative;
                padding-bottom: 50px;
                &::before{
                    content: "";
                    width: 100%;
                    height: 1px;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    background-color: $blue2;
                }
            }
            .entry-type{
                text-transform: capitalize;
                letter-spacing: 1;
                margin-bottom: 15px;
            }
            .entry-title{
                font-size: 25px;
            }
            .entry-content{
                margin: 20px 0;
            }
        }
    }
}