#top{

    .callout--statistics,
    .callout--statistics-vertical{
        background-color: $white;
        box-shadow: 0 15px 60px rgba($color: #CBDBE9, $alpha: 1.0);
        border-radius: 20px;
        .ep-animated-number{
            display: flex;
            flex-direction: column;
            .avia-animated-number-icon{
                font-size: 45px;
                color: $blue3;
                @media only screen and (min-width: 768px){
                    font-size: 55px;
                }
                @media only screen and (min-width: 768px) and (max-width: 1310px){
                    font-size: 4.19vw;
                }
                &::before{
                    line-height: 1;
                }
            }
            .heading{
                order: 1;
                font-size: 30px;
                font-weight: $light;
                @media only screen and (min-width: 768px){
                    font-size: 36px;
                }
                @media only screen and (min-width: 768px) and (max-width: 1310px){
                    font-size: 2.74vw;
                }
            }
            .ep-animated-number-content{
                font-family: $heading-font-family;
                font-weight: $bold;
                color: $pink4;
                text-transform: uppercase;
                line-height: 1;
                font-size: 14px;
                letter-spacing: calc((14px / 1000 ) * 150);
                padding-bottom: 10px;
                @media only screen and (min-width: 768px){
                    font-size: 16px;
                    letter-spacing: calc((16px / 1000 ) * 150);
                    padding-bottom: 12.5px;
                }
                @media only screen and (min-width: 768px) and (max-width: 1310px){
                    font-size: 1.22vw;
                    letter-spacing: calc((1.22vw / 1000 ) * 150);
                    padding-bottom: 1.14vw;
                }
                p{
                    margin-bottom: 0;
                }
            }
        }
    }

    .callout--statistics{
        padding: 40px 7.5vw;
        @media only screen and (min-width: 768px){
            padding: 6.10vw 6.86vw;
        }
        @media only screen and (min-width: 1310px){
            padding: 80px 90px;
        }
        .ep-animated-number{
            align-items: center;
            .avia-animated-number-icon{
                display: flex;
                justify-content: center;
                align-items: flex-start;
                width: max-content;
                height: 70px;
                @media only screen and (min-width: 768px){
                    height: 80px;
                }
                @media only screen and (min-width: 768px) and (max-width: 1310px){
                    height: 6.10vw;
                }
            }
        }
    }

    .callout--statistics-vertical{
        padding: 40px 7.5vw;
        @media only screen and (min-width: 768px){
            padding: 3.81vw 3.05vw;
        }
        @media only screen and (min-width: 1310px){
            padding: 50px 40px;
        }
        .ep-animated-number{
            padding-left: 80px;
            position: relative;
            @media only screen and (min-width: 768px){
                padding-left: 100px;
            }
            @media only screen and (min-width: 768px) and (max-width: 1310px){
                padding-left: 7.63vw;
            }
            .avia-animated-number-icon{
                position: absolute;
                left: 0;
                top: 8px;
                @media only screen and (min-width: 768px) and (max-width: 1310px){
                    top: 0.61vw;
                }
            }
        }
    }

    .animated-number--pill{
        width: 100%;
        max-width: 750px;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        padding: 50px 60px;
        border-radius: 84px;
        @media only screen and (min-width: 768px) and (max-width: 850px){
            padding: 5.88vw 7.05vw;
            border-radius: 9.88vw;
        }
        @media only screen and (max-width: 767px){
            flex-direction: column;
            border-radius: 46px;
            padding: 40px 7.5vw;
        }
        span{
            display: inline-block;
            line-height: 1;
        }
        .avia-animated-number-icon{
            width: 92px;
            margin-bottom: 25px;
            font-size: 66px;
            @media only screen and (min-width: 768px){
                margin-right: 40px;
                margin-bottom: 0;
            }
            @media only screen and (min-width: 768px) and (max-width: 850px){
                width: 10.82vw;
                margin-right: 4.70vw;
                font-size: 7.76vw;
            }
        }
        .heading{
            width: max-content;
            font-weight: $bold;
            margin-bottom: 25px;
            font-size: 40px;
            @media only screen and (min-width: 768px){
                margin-right: 40px;
                margin-bottom: 0;
                font-size: 55px;
            }
            @media only screen and (min-width: 768px) and (max-width: 850px){
                margin-right: 4.70vw;
                font-size: 6.47vw;
            }
        }
        .ep-animated-number-content{
            flex: 1;
            font-weight: $bold;
            text-transform: uppercase;
            font-size: 20px;
            letter-spacing: calc((20px / 1000 ) * 150);
            @media only screen and (min-width: 768px) and (max-width: 850px){
                font-size: 2.35vw;
                letter-spacing: calc((2.35vw / 1000 ) * 150);
            }
            @media only screen and (max-width: 767px){
                text-align: center;
            }
            p:last-child{
                margin-bottom: 0
            }
        }
    }

    .gradient-pill--blue{
        background: linear-gradient(90deg, rgba(94,209,216,1) 0%, rgba(106,136,223,1) 120%);
        box-shadow: 0 16px 60px rgba($color: #38647D, $alpha: 0.27);
        *{
            color: $white;
        }
    }

}