#top{

    .itemgrid--accordion{
        .ep-item-grid-item{
            .ep-item-grid-item-title{
                font-weight: $bold;
                padding-top: 20px;
                padding-bottom: 20px;
                padding-right: 60px;
                position: relative;
                @media only screen and (min-width: 768px){
                    padding-top: 25px;
                    padding-bottom: 25px;
                    padding-right: 100px;
                }
                @media only screen and (min-width: 1100px){
                    padding-right: 150px;
                    padding-top: 35px;
                    padding-bottom: 35px;
                }
                @media only screen and (max-width: 767px){
                    font-size: 20px;
                }
                &::before,
                &::after{
                    content: '\e807';
                    font-family: 'rxpharmacy-icons';
                    border-radius: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    box-shadow: 10px 10px 30px rgba($color: #304D7C, $alpha: 0.24);
                    color: $white;
                    font-weight: $regular;
                    line-height: 1;
                    position: absolute;
                    right: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    width: 34px;
                    height: 34px;
                    font-size: 16px;
                    cursor: pointer;
                    @media only screen and (min-width: 768px){
                        width: 40px;
                        height: 40px;
                        font-size: 20px;
                    }
                    @media only screen and (min-width: 1100px){
                        width: 60px;
                        height: 60px;
                        font-size: 24px;
                    }
                }
                &::before{
                    background-color: $darkblue;
                    transition: opacity ease-in-out 0.2s;
                }
                &::after{
                    background: linear-gradient(90deg, rgba(106,136,223,1) 0%, rgba(200,75,244,1) 100%);
                    opacity: 0;
                    transform: translateY(-50%) rotate(90deg);
                    transition: opacity ease-in-out 0.2s 0.1s;
                }
            }
            .ep-item-grid-item-content{
                line-height: 1.6;
                max-height: 0;
				opacity: 0;
				pointer-events: none;
				overflow: hidden;
				transition: max-height cubic-bezier(0.4, 0, 0.2, 1) 0.3s, height cubic-bezier(0.4, 0, 0.2, 1) 0.3s, overflow ease 0.25s, opacity ease 0.25s;
                font-size: 16px;
                @media only screen and (min-width: 768px){
                    font-size: 18px;
                }
                @media only screen and (min-width: 1100px){
                    font-size: 20px;
                }
                p{
                    @media only screen and (min-width: 768px){
                        padding: 5px 0;
                    }
                    @media only screen and (min-width: 1100px){
                        padding: 15px 0;
                    }
                }
            }
            &.is-active{
                .ep-item-grid-item-title{
                    &::before{
                        opacity: 0;
                        transition: opacity ease-in-out 0.2s 0.1s;
                    }
                    &::after{
                        opacity: 1;
                        transition: opacity ease-in-out 0.2s;
                    }
                }
                .ep-item-grid-item-content{
                    max-height: 900px;
					pointer-events: initial;
					overflow: visible;
					opacity: 1;
					transition-duration: 0.4s;
                }
            }
        }
    }

}