#top{
    .ep-posts-grid {
        .ep-item-grid-item{
            .ep-item-grid-item-inner{
                .ep-item-grid-image{
                    a{
                        img{
                            max-height: 300px;
                            object-fit: cover;
                            object-position: center center;
                            border-radius: 13px;
                            @media only screen and (min-width: 768px){
                                max-height: 195px;
                            }
                        }
                    }
                }
                
                .ep-item-grid-content-wrapper{
                    display: flex;
                    flex-wrap: wrap;
                    .ep-item-grid-item-terms{
                        order: 2;
                        align-self: baseline;
                        a, span{
                            border: 1px solid $pink3;
                            padding: 3px 10px;
                            border-radius: 21px;
                            font-size: 12px;
                            font-family: $extra-font-family;
                            transition: opacity ease 0.2s !important;
                            &:hover{
                                text-decoration: none;
                                opacity: 0.6;
                                
                            }
                        }
                    }
                    .ep-item-grid-item-date{
                        order: 1;
                        margin-right: 20px;
                        font-size: 12px;
                        font-weight: $bold;
                        align-self: flex-end;
                        font-family: $heading-font-family;
                    }
                    .ep-item-grid-item-title-wrapper{
                        width: 100%;
                    }
                    .ep-item-grid-item-title{
                        flex-grow: 1;
                        order: 3;
                        padding: 15px 0px;
                    }
                    .ep-item-grid-item-content{
                        flex-grow: 1;
                        order: 4;
                    }
                    .ep-item-grid-item-button-wrapper{
                        flex-grow: 1;
                        order: 5;
                        padding-top: 30px;
                        .avia-color-arrow{
                            font-size: 16px;
                            &:after{
                                content: '\e803';
                                font-size: 14px;
                            }
                        }
                    }
                }
            }
            // custom download image
            &.category-download{
                .ep-item-grid-image img{
                    object-position: top center !important;
                }
            }
        }
    }
}
// Type colors
#top{
    .ep-posts-grid-item{
        // blog
        &.entry-post{
            .ep-item-grid-item-terms a{
                border-color: $orange !important;
                color: $orange;
            }
        }
        // resources - general
        &.entry-resources{
            .ep-item-grid-item-terms a{
                border-color: $blue3 !important;
                color: $blue3;
            }
        }
        // resources - download
        &.entry-resources{
            .ep-item-grid-item-terms a.ep-item-term-download {
                border-color: $green !important;
                color: $green !important;
            }
        }
        // news - general
        &.entry-news-press{
            .ep-item-grid-item-terms a {
                border-color: $pink !important;
                color: $pink;
            }
        }

         // webinar
         &.entry-webinar{
            .ep-item-grid-item-terms span {
                border-color: $orange !important;
                color: $orange;
            }
        }
    }
}