#top{
    .facetwp-template{
        position: relative;
        &:after{
            content: '';
            width: 50px;
            height: 50px;
            background-image: url(../img/svg/loading.svg);
            background-repeat: no-repeat;
            background-position: center center;
            position: absolute;
            top: 100px;
            left: 50%;
            opacity: 0;
            transform: translateX(-50%);
            pointer-events: none;
            transition: opacity ease 0.3s;
        }

        & > div{
            transition: filter ease 0.2s, opacity ease 0.2s;
        }
        &.fwp-loading{
            &:after{
                opacity: 1;
            }
            & > div{
                filter: blur(8px) grayscale(50%);
                opacity: 0.1;
            }
        }
    }

    .facet-filter{
        display: flex;
        padding-bottom: 5px;
        border-bottom: 1px solid $darkblue2;
        width: fit-content;
        span{
            margin-right: 8px;
            font-family: $body-font-family;
            font-size: 14px;
        }
        .facetwp-type-dropdown{
            margin-bottom: 0;
            position: relative;
            &:before{
                content:'\e800';
                font-family: 'fa-fontello';
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 0;
                color: $darkblue2;
            }
            select{
                -webkit-appearance: none !important;
                background-color: transparent;
                border: 0;
                padding-right: 50px;
                min-width: fit-content;
                color: $darkblue2;
                font-weight: $bold;
                &:focus{
                    outline: none;
                }
            }
        }
    }
    .facetwp-type-pager{
        text-align: center;
        button{
            color: $pink3;
            border-color: $pink3;
            background: none;
            border: 4px solid;
            padding: 13.5px 30.5px !important;
            transition: box-shadow ease 0.25s, color ease 0.25s, border-color ease 0.25s;
            cursor: pointer;
            &:hover{
                color: $purple2;
                border-color: $purple2;
            }
            &.facetwp-hidden{
                display: none !important;
            }
        }
    }
}