#top{
    .boxed-item-grid{
        max-width: 1000px;
        margin: 0 auto;
        @media only screen and (max-width: 767px){
            margin-right: -7.5vw;
        }
        .ep-item-grid{
            justify-content: center;
            position: relative;
            &::after{
                content: 'flickity';
                display: none;
                @media only screen and (min-width: 768px){
                    content: '';
                }
            }
            .flickity-page-dots{
                max-width: calc(100% - 13vw);
            }
        }
        .ep-item-grid-item{
            position: relative;
            @media only screen and (max-width: 767px){
                width: 85%;
            }
            &:not(:last-child){
                &::after{
                    content: '\e807';
                    font-family: 'rxpharmacy-icons';
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%) translateX(50%);
                    right: 0;
                    width: 40px;
                    height: 40px;
                    border-radius: 100%;
                    background-color: $darkblue2;
                    z-index: 1;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    color: $white;
                    box-shadow: 10px 10px 30px rgba(48, 77, 124, 0.24);
                    @media only screen and (max-width: 1023px){
                        opacity: 0;
                    }
                    @media only screen and (max-width: 768px){
                        top: calc(100% - 20px);
                        right: auto;
                        left: 50%;
                        transform: translateX(-50%) rotate(90deg);
                    }
                }			
            }
            .ep-item-grid-item-inner{
                background-color: $white;
                border-radius: 20px;
                display: flex;
                flex-direction: column;
                padding: 30px;
                margin: 0 auto;
                box-shadow: 0px 15px 60px rgba($color: $lightblue5, $alpha: 1.0);
                min-height: 321px;
                @media only screen and (max-width: 767px){
                    box-shadow: 0px 5px 10px rgba($color: $lightblue5, $alpha: 0.7);
                }
                .ep-item-grid-item-media{
                    margin-bottom: 40px;
                    .ep-item-grid-icon{
                        text-align: right;
                        font-size: 43px;
                        color: $blue3;
                        font-weight: $bold;
                    }
                    img{
                        margin: 0 0 0 auto;
                    }
                }
                .ep-item-grid-content-wrapper{
                    .ep-item-grid-item-content{
                        margin-top: 12px;
                        font-size: 14px;
                        line-height: 1.642;
                    }
                }
            }
        }
        &.alter-boxed-item{
            max-width: 100%;
            .ep-item-grid-item-inner{
                padding-top: 50px;
                padding-bottom: 20px;
                .ep-item-grid-item-media{
                    .ep-item-grid-icon{
                        text-align: left;
                    }
                }
            }
            &[style*="--itemCount: 4"],
            &[style*="--itemCount: 5"],
            &[style*="--itemCount: 6"]{
                .ep-item-grid{
                    .ep-item-grid-item{
                        @media only screen and (min-width: 1024px) and (max-width: 1199px){
                            width: 33.33%;
                            &:nth-last-child(3n){
                                &::after{
                                    display: none;
                                }
                            }
                        }
                    }
                }   
            }
        }
    }
}