#top{
    .gradient-pill{
        .ep-item-grid-item-inner{
            margin: 0 auto;
            max-width: 593px;
            width: 100%;
            border-radius: 83.5px;
            background: linear-gradient(90deg, #c84bf4 0%, #6a88df 100%);
            box-shadow: 0px 16px 60px rgba(56, 100, 125, 0.27);
            min-height: 137px;
            display: flex;
            justify-content: center;
            align-items: center;
            @media only screen and (min-width: 768px){
                min-height: 167px;
            }
            .ep-item-grid-content-wrapper{
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 100%;
                padding: 0 20px;
                @media only screen and (max-width: 767px){
                    flex-direction: column;
                }
                .ep-item-grid-item-title{
                    font-size: 45px;
                    color: $white;
                    position: relative;
                    padding-right: 30px;
                    line-height: 1.2;
                    @media only screen and (min-width: 768px){
                        font-size: 76px;
                        padding-right: 37px;
                        margin-left: 44px;
                        margin-top: 12px;
                    }
                    span{
                        font-size: 30px;
                        position: absolute;
                        top: 0;
                        right: 0;
                        @media only screen and (min-width: 768px){
                            font-size: 42px;
                        }
                    }
                }
                .ep-item-grid-item-content{
                    text-align: center;
                    @media only screen and (min-width: 768px){
                        text-align: left;
                        max-width: 283px;
                        padding-left: 20px;
                        margin-right: 50px;
                    }
                    h4{
                        color: $white;
                        text-transform: uppercase;
                        font-size: 14px;
                        @media only screen and (min-width: 768px){
                            font-size: 20px;
                        }
                    }
                }
            }
        }
    }
}

#top{

    .has-purple-gradient{
        background: linear-gradient(142deg, #c84bf4 0%, #6a88df 100%);
    }

}