#top{
    .icon-boxed-grid{
        .ep-item-grid-item{
            .ep-item-grid-item-inner{
                display: flex;
                .ep-item-grid-item-media{
                    .ep-item-grid-icon{
                        width: 70px;
                        height: 70px;
                        width: 70px;
                        background: linear-gradient(#69b3f5 0%, #5ed1d8 100%);
                        box-shadow: 0px 15px 30px rgba(56, 100, 125, 0.27);
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        border-radius: 16px;
                        color: $white;
                        font-size: 30px;
                    }
                }
                .ep-item-grid-content-wrapper{
                    .ep-item-grid-item-content{
                        font-size: 16px;
                        line-height: 1.75;
                        @media only screen and (min-width: 768px){
                            max-width: 343px;
                        }
                    }
                }
            }
            &:first-child{
                .ep-item-grid-item-inner{
                    @media only screen and (min-width: 768px){
                        flex-direction: row-reverse;
                    }
                    .ep-item-grid-content-wrapper{
                        margin-left: 30px;
                        @media only screen and (min-width: 768px){
                            margin-right: 30px;
                            margin-left: 0;
                        }
                        .ep-item-grid-item-content{
                            p{
                                @media only screen and (max-width: 767px){
                                    text-align: left!important;
                                }
                            }
                        }
                    }
                }
            }
            &:last-child{
                .ep-item-grid-item-inner{
                    .ep-item-grid-content-wrapper{
                        margin-left: 30px;
                    }
                }
            }
        }
    }
}