/**
Footer.scss
Create a Page for a footer, then assign section CSS class `footer-section`
**/
#top{
    .footer-page-content,
    .footer-page-content .footer-section{
        border-top: 0;
    }
    .footer-section{
        background: $darkblue;
        position: relative;
        padding-top: 9px;
        &::before{
            content: "";
            width: 100%;
            height: 9px;
            position: absolute;
            top: 0;
            left: 0;
            background: linear-gradient(45deg, #c84bf4 0%, #6a88df 100%);
        }
        .container{
            padding-top: 0 !important;
            .ep-social-bookmarks-wrapper{
                margin-top: 30px;
            }
        }
        .flex_column_table{
            &.footer-first-row{
                margin-bottom: 50px;
                .logo{
                    display: block;
                    max-width: 270px;
                    img{
                        width: 100%;
                        display: block;
                    }
                }
            }
            &.footer-second-row{
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                .flex_column{
                    &:first-child{
                        width: 45%;
                        @media only screen and (max-width: 767px){
                            width: 100%;
                        }
                        .flex-column-inner{
                            display: flex;
                            flex-wrap: wrap;
                            .av_textblock_section{
                                margin-bottom: 50px;
                            }
                            .avia-button-wrap{
                                
                            }
                            .ep-social-bookmarks-wrapper{
                                @media only screen and (max-width: 362px){
                                    margin-top: 30px;
                                }
                                margin-top: 0;
                                align-items: center;
                                margin-left: 30px;
                                ul{
                                    align-items: center;
                                    height: 100%;
                                }
                            }
                        }
                    }
                    &:last-child{
                        width: 46%;
                        @media only screen and (max-width: 767px){
                            width: 100%;
                        }
                        .flex-column-inner{
                            @media only screen and (max-width: 989px){
                                margin-top: 50px;
                            }
                            .menu{
                                display: flex;
                                justify-content: space-between;
                                flex-wrap: wrap;
                                & > li{
                                    &:not(:nth-last-child(-n+2)){
                                        @media only screen and (max-width: 767px){
                                            margin-bottom: 20px;
                                        }
                                    }
                                    &:nth-last-child(odd){
                                        @media only screen and (max-width: 767px){
                                            margin-right: 10%;
                                        }
                                    }
                                    @media only screen and (max-width: 767px){
                                        width: 45%;
                                    }
                                    & > a{
                                        display: block;
                                        color: $pink3;
                                        font-size: 18px;
                                        font-weight: $bold;
                                        margin-bottom: 5px;
                                    }
                                    .sub-menu{
                                        display: block;
                                        display: flex;
                                        flex-direction: column;
                                        li{
                                            margin: 10px 0;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            &.footer-socket{
                position: relative;
                font-size: 12px;
                margin-top: 20px;
                @media only screen and (min-width: 768px){
                    margin-top: 90px;
                    padding-top: 50px;
                }
                p, a{
                    color: rgba($color: $white, $alpha: 0.41);
                    text-decoration: none;
                }
                &::before{
                    content: "";
                    width: 100%;
                    height: 6px;
                    background-color: rgba($color: $white, $alpha: 0.07);
                    position: absolute;
                    top: 0;
                    left: 50%;
                    transform: translateX(-50%);
                    border-radius: 10px;
                    @media only screen and (max-width: 767px){
                        opacity: 0;
                    }
                }
            }
        }
    }
}