#top{
    .paralax-section{
        position: relative;
        &::before{
            content: "";
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0;
            background-color: $lightblue3;
            background-image: url('../img/png/parallax-bg.png');
            background-blend-mode: overlay;
            background-attachment: fixed;
        }
    }
}