#top{

    .itemgrid--gradient-arrow{
        .ep-item-grid-item{
            @media only screen and (min-width: 768px) and (max-width: 989px){
                --epCustomItemSize: 45px !important;
            }
            .ep-item-grid-item-inner{
                display: flex;
                flex-direction: column;
                border-radius: 10px;
                background: linear-gradient(90deg, rgba(94,209,216,1) 0%, rgba(106,136,223,1) 120%);
                box-shadow: 0 16px 60px rgba($color: #38647D, $alpha: 0.27);
                padding: 35px 50px 35px 7.5vw;
                @media only screen and (min-width: 768px){
                    border-radius: 15px;
                    flex-direction: row;
                    align-items: center;
                    padding: 35px 6.10vw 35px 3.81vw;
                }
                @media only screen and (min-width: 1310px){
                    border-radius: 20px;
                    padding: 40px 80px 40px 50px;
                }
                .ep-link-wrapper{
                    padding: inherit;
                    margin: -35px -50px -35px -7.5vw;
                    position: relative;
                    width: calc( 100% + ( 50px + 7.5vw ) );
                    max-width: unset;
                    @media only screen and (min-width: 768px){
                        margin: -35px -6.10vw -35px -3.81vw;
                        width: calc( 100% + ( 6.10vw + 3.81vw ) );
                    }
                    @media only screen and (min-width: 1310px){
                        margin: -40px -80px -40px -50px;
                        width: calc( 100% + ( 80px + 50px ) );
                    }
                    &::after{
                        content: '\e807';
                        font-family: 'rxpharmacy-icons';
                        border-radius: 100%;
                        background-color: $white;
                        box-shadow: 10px 10px 30px rgba($color: #304D7C, $alpha: 0.24);
                        color: $darkblue;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        position: absolute;
                        top: 50%;
                        right: 0;
                        transform: translateY(-50%) translateX(50%);
                        width: 45px;
                        height: 45px;
                        font-size: 18px;
                        @media only screen and (min-width: 768px){
                            width: 52px;
                            height: 52px;
                            font-size: 22px;
                        }
                        @media only screen and (min-width: 1310px){
                            width: 62px;
                            height: 62px;
                            font-size: 26px;
                        }
                    }
                }
                .ep-item-grid-item-media{
                    color: $white;
                    margin-bottom: 20px !important;
                    @media only screen and (min-width: 768px){
                        margin-bottom: 0 !important;
                    }
                    & + .ep-item-grid-content-wrapper{
                        @media only screen and (min-width: 768px){
                            margin-left: calc( 9.16vw - var(--epCustomItemSize) );
                        }
                        @media only screen and (min-width: 1310px){
                            margin-left: calc( 120px - var(--epCustomItemSize) );
                        }
                    }
                }
                .ep-item-grid-content-wrapper{
                    flex: 1;
                    .ep-item-grid-item-title{
                        color: $white;
                        margin-bottom: 10px;
                    }
                    .ep-item-grid-item-content{
                        color: $white;
                        font-size: var(--epItemContentSize, 14px);
                        line-height: 1.6;
                        h1, h2, h3, h4, h5, h6,
                        .h1, .h2, .h3, .h4, .h5, .h6{
                            font-weight: $regular;
                        }
                        p{
                            margin-bottom: 0;
                        }
                        strong{
                            color: inherit;
                        }
                    }
                }
            }
        }
        &.itemgrid--purplegradient-arrow{
            .ep-item-grid-item{
                .ep-item-grid-item-inner{
                    background: linear-gradient(90deg, rgba(200,75,244,1) 0%, rgba(106,136,223,1) 120%);
                    .ep-link-wrapper{
                        &::after{
                            background-color: $darkblue;
                            color: $white;
                        }
                    }
                }
            }
        }
    }

    .itemgrid--boxed-simple{
        .ep-item-grid-item{
            .ep-item-grid-item-inner{
                background-color: $white;
                width: 100%;
                height: 100%;
                border-radius: 10px;
                box-shadow: 0 15px 60px rgba($color: #CBDBE9, $alpha: 1.0);
                padding: 40px 7.5vw;
                position: relative;
                transition: opacity ease 0.25s;
                @media only screen and (min-width: 768px){
                    padding: 45px 35px;
                    border-radius: 15px;
                }
                @media only screen and (min-width: 1310px){
                    border-radius: 20px;
                }
                .ep-link-wrapper{
                    padding: inherit;
                    margin: -40px -7.5vw;
                    position: inherit;
                    max-width: unset;
                    width: calc( 100% + 15vw );
                    height: calc( 100% + 80px );
                    @media only screen and (min-width: 768px){
                        margin: -45px -35px;
                        width: calc( 100% + 70px );
                        height: calc( 100% + 90px );
                    }
                }
                .ep-item-grid-item-media{
                    margin-bottom: 20px !important;
                    @media only screen and (min-width: 768px){
                        position: absolute;
                        left: 35px;
                        top: 117.5px;
                        transform: translateY(-100%);
                    }
                }
                .ep-item-grid-content-wrapper{
                    @media only screen and (min-width: 768px){
                        padding-top: 90px;
                    }
                    .ep-item-grid-item-content{
                        p{
                            margin-bottom: 0;
                        }
                    }
                }
                &:hover{
                    opacity: 0.7;
                }
            }
        }
    }

    .itemgrid--key-takeaways{
        .ep-item-grid-item{
            .ep-item-grid-item-inner{
                min-height: 220px;
                width: 100%;
                height: 100%;
                border-radius: 10px;
                box-shadow: 0 15px 60px rgba($color: #304D7C, $alpha: 1.0);
                padding: 30px 7.5vw;
                position: relative;
                background-color: $white;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                @media only screen and (min-width: 768px){
                    padding: 30px;
                    border-radius: 15px;
                }
                @media only screen and (min-width: 1310px){
                    border-radius: 20px;
                }
                .ep-link-wrapper{
                    padding: inherit;
                    margin: -30px -7.5vw;
                    position: inherit;
                    max-width: unset;
                    width: calc( 100% + 15vw );
                    height: calc( 100% + 60px );
                    @media only screen and (min-width: 768px){
                        margin: -30px;
                        width: calc( 100% + 60px );
                        height: calc( 100% + 60px );
                    }
                }
                .ep-item-grid-content-wrapper{
                    margin-top: 20px;
                }
            }
        }
    }

    .itemgrid--checkmark{
        .ep-item-grid-item{
            .ep-item-grid-item-inner{
                width: 100%;
                height: 100%;
                border-radius: 10px;
                box-shadow: 0 7.5px 40px rgba($color: #CBDBE9, $alpha: 1.0);
                padding: 30px 7.5vw;
                position: relative;
                background-color: $white;
                display: flex;
                flex-direction: row-reverse;
                justify-content: space-between;
                align-items: center;
                @media only screen and (min-width: 768px){
                    padding: 35px;
                    border-radius: 15px;
                }
                @media only screen and (min-width: 1310px){
                    border-radius: 20px;
                }
                .ep-link-wrapper{
                    padding: inherit;
                    margin: -30px -7.5vw;
                    position: inherit;
                    max-width: unset;
                    width: calc( 100% + 15vw );
                    height: calc( 100% + 60px );
                    @media only screen and (min-width: 768px){
                        margin: -35px;
                        width: calc( 100% + 70px );
                        height: calc( 100% + 70px );
                    }
                }
                .ep-item-grid-item-media{
                    margin-left: 25px;
                    .ep-item-grid-icon{
                        font-size: 30px;
                        line-height: 1;
                        color: $purple;
                    }
                }
                .ep-item-grid-item-title{
                    @media only screen and (max-width: 370px){
                        font-size: 16px;
                    }
                }
            }
        }
    }

    .itemgrid--pill{
        .ep-item-grid{
            justify-content: center;
            .ep-item-grid-item{
                max-width: calc( 370px + ( var(--columnGap) * 2 ) );
                .ep-item-grid-item-inner{
                    background: linear-gradient(90deg, rgba(200,75,244,1) 0%, rgba(106,136,223,1) 100%);
                    box-shadow: 0 15px 60px rgba($color: #CBDBE9, $alpha: 1.0);
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    padding: 20px 40px;
                    border-radius: 68px;
                    min-height: 135px;
                    @media only screen and (min-width: 1200px){
                        padding: 20px 60px;
                    }
                    .ep-link-wrapper{
                        padding: inherit;
                        position: inherit;
                        max-width: unset;
                        margin: -30px -40px;
                        width: calc( 100% + 80px );
                        height: calc( 100% + 60px );
                        @media only screen and (min-width: 1200px){
                            margin: -40px -60px;
                            width: calc( 100% + 120px );
                            height: calc( 100% + 80px );
                        }
                    }
                    .ep-item-grid-item-media{
                        margin-bottom: 0 !important;
                        color: $white;
                        @media only screen and (min-width: 768px){
                            width: auto;
                        }
                    }
                    .ep-item-grid-content-wrapper{
                        margin-left: 35px;
                        flex: 1;
                        @media only screen and (max-width: 767px){
                            margin-left: calc( 90px - var(--epCustomItemSize) );
                        }
                        .ep-item-grid-item-title{
                            color: $white;
                            @media only screen and (max-width: 767px){
                                text-align: left;
                            }
                        }
                        .ep-item-grid-item-content{
                            @media only screen and (max-width: 767px){
                                text-align: left;
                            }
                        }
                    }
                }
            }
        }
    }

}