#top{
	.custom-featured-post{
        .ep-posts-grid{
            .ep-item-grid-item{
                .ep-item-grid-item-inner{
                    display: flex;
                    position: relative;
                    align-items: center;
                    padding: 65px 65px 65px 0;
                    @media only screen and (max-width: 989px){
                        flex-direction: column;
                        padding: 65px;
                    }
                    @media only screen and (max-width: 767px){
                        padding: 25px;
                    }
                    &::before{
                        content: '';
                        width: calc( 100% - 100px );
                        height: 100%;
                        position: absolute;
                        top: 0;
                        right: 0;
                        border-radius: 20px;
                        background-color: $white;
                        box-shadow: 0px 60px 60px rgba(52, 55, 65, 0.11);
                        @media only screen and (max-width: 989px){
                            width: 100%;
                        }
                    }
                    .ep-item-grid-item-media,
                    .ep-item-grid-content-wrapper{
                        position: relative;
                    }
                    .ep-item-grid-item-media{
                        max-width: 370px;
                        width: 100%;
                        @media only screen and (max-width: 989px){
                            margin-bottom: 50px;
                        }
                        @media only screen and (min-width: 990px) and (max-width: 1219px){
                            margin-right: 80px;
                        }
                        @media only screen and (min-width: 1220px){
                            margin-right: 99px;
                        }
                        .ep-item-grid-image{
                            width: 100%;
                            padding-bottom: 81.62%;
                            img{
                                border-radius: 20px;
                                position: absolute;
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                                max-height: none;
                            }
                        }
                    }
                    .ep-item-grid-content-wrapper{
                        display: block;
                        @media only screen and (min-width: 990px){
                            width: calc(100% - 370px);
                        }
                        .ep-item-grid-item-date{
                            font-size: 12px;
                            font-weight: $bold;
                            letter-spacing: calc(( 12px / 1000 ) * 150 );
                            font-family: $heading-font-family;
                            margin-bottom: 14px;
                            @media only screen and (max-width: 767px){
                                margin-bottom: 10px;
                            }
                            .featured-term{
                                letter-spacing: 0;
                                font-size: 12px;
                                font-weight: $regular;
                                display: inline-block;
                                margin-left: 10px;
                                min-width: 57px;
                                border: 1px solid $orange;
                                text-align: center;
                                border-radius: 21px;
                                font-family: $extra-font-family;
                                color: $orange;
                                line-height: 1.2;
                                padding: 2px 10px;
                            }
                        }
                        .ep-item-grid-item-title{
                            font-weight: $bold;
                            margin-bottom: 12px;
                            font-size: 28px;
                            padding-top: 0;
                            padding-bottom: 0;
                            @media only screen and (max-width: 767px){
                                margin-bottom: 10px;
                                line-height: 1.2;
                            }
                        }
                        .ep-item-grid-item-content{
                            font-size: 16px;
                            margin-bottom: 26px;
                            @media only screen and (max-width: 767px){
                                margin-bottom: 10px;
                            }
                        }
                        .ep-item-grid-item-button-wrapper{
                            width: 100%;
                            padding-top: 0;
                            .avia-button{
                                font-size: 16px;
                                font-weight: $bold;
                                &::after{
                                    content: '\e803';
                                    font-family: 'rxpharmacy-icons';
                                    font-size: 12px;
                                }
                            }
                        }
                    }
                }
                &.fallback-image{
                    .ep-item-grid-item-media{
                        .ep-item-grid-image{
                            background-color: $darkblue;
                            border-radius: 20px;
                            filter: drop-shadow(0px 60px 60px rgba(0, 0, 0, 0.06));
                            img{
                                object-fit: contain;
                                padding: 50px;
                            }
                        }
                    }
                    &.category-video{
                        .ep-item-grid-item-media{
                            .ep-item-grid-image{
                                background-color: transparent;
                                background: linear-gradient(220deg, #c84bf4 0%, #6a88df 100%);
                            }
                        }
                        .ep-item-grid-item-inner{
                            &::after{
                                
                            }
                        }
                    }
                }
                &.category-video{
                    .ep-item-grid-item-media{
                        position: relative;
                        &::before,
                        &::after{
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);
                            z-index: 1;
                            border-radius: 100%;
                            background-color: $white;
                            pointer-events: none;
                        }
                        &::before{
                            content: "";
                            width: 116px;
                            height: 116px;
                            opacity: 0.55;
                        }
                        &::after{
                            content: '\e806';
                            font-family: 'fa-fontello';
                            color: $blue;
                            width: 75px;
                            height: 75px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            font-size: 25px;
                        }
                    }
                    .ep-item-grid-content-wrapper{
                        .ep-item-grid-item-date{
                            .featured-term{
                                color: $blue3;
                                border-color: $blue3;   
                            }
                        }
                    }
                }
                &.category-download{
                    .ep-item-grid-item-inner{
                        justify-content: flex-end;
                        .ep-item-grid-item-media{
                            max-width: 343px;
                            margin-right: 0;
                            @media only screen and (min-width: 989px){
                                position: absolute;
                                left: 0;
                                top: 50%;
                                transform: translateY(-50%);
                            }
                            &::before{
                                content: "";
                                width: 100%;
                                height: 100%;
                                position: absolute;
                                top: 0;
                                left: 0;
                                transform: rotate(-7deg);
                                border-radius: 13px;
                                background: linear-gradient(#5ed1d8 0%, #6a88df 100%);
                                box-shadow: 0px 60px 60px rgba(0, 0, 0, 0.06)
                            }
                            .ep-item-grid-image{
                                padding-bottom: 139%;
                            }
                        }
                        .ep-item-grid-content-wrapper{
                            @media only screen and (min-width: 990px){
                                width: calc( 100% - 450px );
                            }
                            .ep-item-grid-item-date{
                                .featured-term{
                                    color: $green;
                                    border-color: $green;   
                                }
                            }
                        }
                    }
                }
            }
        }
	}
}