#top div .avia-button,
#top .button,
.wp-block-button a,
.btTxt,
.submit-search,
.facetwp-load-more {
    /*
    * Generic styles go here
    */

    display: inline-block;
    min-width: 213px;
    padding: 17.5px 30.5px;
    border-radius: 30px;
    background: linear-gradient(90deg, #c84bf4 0%, #6a88df 100%);
    box-shadow: 0px 15px 30px rgba(56, 100, 125, 0.27);
    text-align: center;
    color: $white;
    text-transform: uppercase;
    font-weight: $bold;
    font-family: $heading-font-family;
    font-size: 14px;
    line-height: 1.1;
    letter-spacing: calc(( 14px / 1000 ) * 100 );
    transition: box-shadow ease 0.25s;
    /*
    * State styles go here
    */
    &:hover{
        text-decoration: none;
        box-shadow: 0px 5px 15px rgba(56, 100, 125, 0.27);

    }
    &:active{

    }
}
#top div .avia-button,
.facetwp-load-more { 
    /*
    * Button sizes go here
    */

    &.avia-color-solid-secondary{
        background: linear-gradient(90deg, #5ed1d8 0%, #6a88df 100%);
    }

    &.avia-color-border-primary,
    &.avia-color-border-secondary{
        background: none;
        border: 4px solid;
        padding: 13.5px 30.5px;
        transition: box-shadow ease 0.25s, color ease 0.25s, border-color ease 0.25s;
    }

    &.avia-color-arrow{
        background: none;
        border: none;
        box-shadow: none;
        padding: 0;
        color: $pink3;
        text-align: left;
        text-transform: none;
        font-size: 18px;
        font-family: $body-font-family;
        letter-spacing: 0;
        &::after{
            content: '\e807';
            font-family: 'rxpharmacy-icons';
            margin-left: 13px;
            transition: margin ease 0.25s;
        }
        &:hover{
            &::after{
                margin-left: 20px;
            }
        }
    }


    /*
    * Button styles go here
    */
}



#top .main_color .avia-button {

    &.avia-color-border-primary{
        color: $pink3;
        border-color: $pink3;
        &:hover{
            color: $purple2;
            border-color: $purple2;
        }
    }

    &.avia-color-border-secondary{
        color: $blue3;
        border-color: $blue3;
        &:hover{
            color: $lightblue4;
            border-color: $lightblue4;
        }
    }

}

#top .alternate_color .avia-button {

    &.avia-color-border-primary{
        color: $white;
        border-color: $white;
        &:hover{
            border-color: rgba($color: $white, $alpha: 0.5);
        }
    }

    &.avia-color-border-secondary{
        color: $white;
        border-color: $lightblue4;
        &:hover{
            border-color: rgba($color: $lightblue4, $alpha: 0.5);
        }
    }
	
}